import React, {Component} from 'react';
import {Modal, Button, Row, Col, Form} from 'react-bootstrap';
import 'react-dates/initialize';
import 'react-dates/lib/css/_datepicker.css';
import { DateRangePicker} from 'react-dates';
export class AddVoucherModal extends Component{
    constructor(props){
        super(props);
        this.state={
            startDate:null,
            endDate:null
        }
        this.handleSubmit = this.handleSubmit.bind(this);
    }

     
    converts(str) {
        var date = new Date(str),
          mnth = ("0" + (date.getMonth() + 1)).slice(-2),
          day = ("0" + date.getDate()).slice(-2);
        return [ day,mnth, date.getFullYear()].join("-");
      }
    handleSubmit(event){
        
        event.preventDefault();
        // console.log()
        // console.log(JSON.stringify(
        //     {
        //         "voucherCode": event.target.voucherCode.value,
        //         "name": event.target.name.value,
        //         "description": event.target.description.value,
        //         "price": event.target.price.value,
        //         "begin": this.converts(this.state.startDate._d),
        //         "end": this.converts(this.state.endDate._d)
        //     }
        // ))
        let token = JSON.parse(localStorage.getItem("data")).token

        fetch('https://divine-sky-478f.datntps09954.workers.dev/?https://young-dusk-10057.herokuapp.com/api/v1/voucher/'+token+"/add",{
            method:'POST',
            headers:{
                'Accept':'application/json',
                'Content-Type':'application/json'
            },
            body:JSON.stringify(
                
                {
                    voucherCode: event.target.voucherCode.value,
                    name: event.target.name.value,
                    description: event.target.description.value,
                    price: event.target.price.value,
                    begin:  this.converts(this.state.startDate._d),
                    end: this.converts(this.state.endDate._d)
                }
            )
        })
        .then(res=> res.json())
        .then((result)=>
        {
            alert(JSON.stringify(result.status));
            this.props.refreshList()
            this.props.onHide()
        },)
    }

    render(){
        return(
            <div className="container">
            <Modal
        {...this.props}
        size="lg"
        aria-labelledby="contained-modal-title-vcenter"
        centered
        >
        <Modal.Header closeButton>
            <Modal.Title id="contained-modal-title-vcenter">
             Thm m khuyn mi
            </Modal.Title>
        </Modal.Header>
        <Modal.Body>
            

                <Row>
                    <Col sm={6}>
                    <Form onSubmit={this.handleSubmit}>
                    <Form.Group>
                    <Form.Label>Thi Gian</Form.Label>
                    <br/>
                    {/* <Form.Control> */}
                    <DateRangePicker
                    startDate={this.state.startDate} // momentPropTypes.momentObj or null,
                    startDateId="your_unique_start_date_id" // PropTypes.string.isRequired,
                    endDate={this.state.endDate} // momentPropTypes.momentObj or null,
                    endDateId="your_unique_end_date_id" // PropTypes.string.isRequired,
                    onDatesChange={({ startDate, endDate }) => this.setState({ startDate, endDate })} // PropTypes.func.isRequired,
                    focusedInput={this.state.focusedInput} // PropTypes.oneOf([START_DATE, END_DATE]) or null,
                    onFocusChange={focusedInput => this.setState({ focusedInput })} // PropTypes.func.isRequired,
                    required

                    />
                    {/* </Form.Control> */}
                    </Form.Group>
                    <Form.Group controlID="voucherCode">
                    <Form.Label>M voucher</Form.Label>
                    <Form.Control
                        type="text"
                        name="voucherCode"
                        required
                        placeholder="M voucher"
                    />
                    </Form.Group>
                    <Form.Group controlID="name">
                    <Form.Label>Tn dch v</Form.Label>
                    <Form.Control
                        type="text"
                        name="name"
                        required
                        placeholder="Tn dch v"
                    />
                    </Form.Group>
                    <Form.Group controlID="description">
                    <Form.Label>M t</Form.Label>
                    <Form.Control
                        type="text"
                        name="description"
                        required
                        placeholder="M t"
                    />
                    </Form.Group>
                    
                    <Form.Group controlID="price">
                    <Form.Label>Gi gim</Form.Label>
                    <Form.Control
                        type="price"
                        name="price"
                        required
                        placeholder="Gi gim"
                    />
                    </Form.Group>

                    <Form.Group>
                        <Button variant="primary" type="submit">
                            Thm
                        </Button>
                    </Form.Group>
                    </Form>
                    </Col>

                </Row>
        </Modal.Body>
        <Modal.Footer>
            <Button variant="danger" onClick={this.props.onHide}>Close</Button>
        </Modal.Footer>
        </Modal>
        </div>
        );
    }

}